<template lang="html">
	<div class="moneyDetails">
		<h3></h3>
		<form class="form-inline" role="form" style="margin-left: 30px; min-width: 750px;">
			<div class="form-group">
		   		<nav>
				  <ul class="pagination" style="margin-top: 25px;" >
				    <li><a class="btn btn-link" @click="findMoney('')"></a></li>
				    <li><a class="btn btn-link" @click="findMoney('PAYMENT')"></a></li>
				    <li><a class="btn btn-link" @click="findMoney('SENDING')"></a></li>
				    <li><a class="btn btn-link" @click="findMoney('SERVER')"></a></li>
				    <li><a class="btn btn-link" @click="findMoney('REWORK')"></a></li>
				    <li><a class="btn btn-link" @click="findMoney('WAIT_COMMENT')"></a></li>
				    <li><a class="btn btn-link" @click="findMoney('FINISH')"></a></li>
				    <li><a class="btn btn-link" @click="findMoney('CLOSE')"></a></li>
				  </ul>
				</nav>
		    </div>
		    <div class="form-group">
			    <label class="sr-only"></label>
			    <input type="text" class="form-control" placeholder="/" v-model="photoName">
			</div>
		    <button type="button" class="btn btn-default btn-sm" @click="findMoney('')"></button>
		</form>
		<table class="table table-bordered table-hover text-center" style="font-size: 14px;">
		  <tr>
		  	<th style="min-width: 120px;"></th>
		  	<th style="min-width: 74px;"></th>
		  	<th style="min-width: 125px;">/</th>
		  	<th style="min-width: 155px;"></th>
		  	<th style="min-width: 265px;">//</th>
		  	<th style="min-width: 101px;"></th>
		  	<th style="min-width: 155px;"></th>
		  	<th style="min-width: 102px;"></th>
		  	<!-- <th style="min-width: 123px;"></th> -->
		  	<th style="min-width: 74px;"></th>
		  	<th style="min-width: 64px;"></th>
		  </tr>
		  <tr v-for="moneyData in moneyData.list">
		  	<template  v-if="!show">
		  		<td>{{moneyData.photoNickName}}</td>
			  	<td>{{moneyData.title}}</td>
			  	<td>
			  		{{moneyData.designNum}}/{{moneyData.designPrice}}<br />
			  		{{moneyData.photoNum}}/{{moneyData.photoPrice}}<br />
			  	</td>
			  	<td>{{moneyData.createTime}}</td>
			  	<td>
			  		<small v-show="moneyData.isUrgent =='YES'"> {{moneyData.urgentPrice}}/{{moneyData.urgentTime}}</small>
			  		<small v-if="moneyData.orderType=='ALL'">  /96</small>
			  		<small v-if="moneyData.orderType!='ALL'">  /72</small>
			  		<br /><strong v-if="moneyData.startTime" v-html="moneyData.countdown" style="color: royalblue;font-size: 12px;"></strong><strong v-if="!moneyData.startTime" style="color: royalblue;font-size: 12px;"></strong>
			  	</td>
			  	<td>
			  		<template v-if="moneyData.orderStatus == 'PAYMENT'"></template>
<template v-if="moneyData.orderStatus == 'WAIT_UPLOAD'"></template>
<template v-if="moneyData.orderStatus == 'SENDING'"></template>
<template v-if="moneyData.orderStatus == 'SERVER'"></template>
<template v-if="moneyData.orderStatus == 'WAIT_CHECK'"></template>
<template v-if="moneyData.orderStatus == 'WAIT_CONFIRM'"></template>
<template v-if="moneyData.orderStatus == 'FINISH'"></template>
<template v-if="moneyData.orderStatus == 'REWORK'"></template>
<template v-if="moneyData.orderStatus == 'WAIT_COMMENT'"></template>
<template v-if="moneyData.orderStatus == 'CLOSE'"></template>
</td>
<td>
    <template v-if="moneyData.workId == 0 && moneyData.orderStatus != 'WAIT_UPLOAD'"> <strong style="color: #5bc0de;"></strong> </template>
    <template v-if="moneyData.workId != 0 && moneyData.startTime == '' "> <strong style="color: #ff0700;"></strong> </template>
    <template v-if="moneyData.workId != 0 && moneyData.startTime != '' "> {{moneyData.startTime}} </template>
    <template v-if="moneyData.orderStatus == 'WAIT_UPLOAD'">  </template>
    <td>
        <template v-if="moneyData.workId == 0 && moneyData.orderStatus != 'WAIT_UPLOAD'"> <strong style="color: #5bc0de;"></strong> </template>
        <template v-if="moneyData.workId != 0"> {{moneyData.workNickName}} </template>
        <template v-if="moneyData.orderStatus == 'WAIT_UPLOAD'">  </template>
    </td>
    <!-- <td>
			  		<template v-if="moneyData.orderStatus != 'CLOSE'">
				  		<template v-if="moneyData.orderStatus == 'WAIT_UPLOAD'"></template>
				  		<template v-if="moneyData.orderStatus != 'WAIT_UPLOAD'">
				  			<a href="{{moneyData.files != ''?moneyData.files:''}}" target="_blank"><button type="button" class="btn btn-link"></button></a>
				  			<button type="button" class="btn btn-success btn-sm" data-toggle="modal" data-target=".yulan" @click="findOneorder(moneyData.orderId)"></button>
				  		</template>
			  		</template>
			  		<template v-if="moneyData.orderStatus == 'CLOSE'">
			  			
			  		</template>
			  	</td> -->
    <td><a v-link="{path :'orderDetails?orderId='+moneyData.orderId}"></a></td>
    <td>
        <template v-if="moneyData.workId == 0 && moneyData.orderStatus != 'CLOSE'">
			  			<button type="button" class="btn btn-danger btn-sm" data-toggle="modal" data-target=".chedan" @click="gitid(moneyData.orderId)"></button>
			  		</template>
        <template v-if="moneyData.workId == 0 && moneyData.orderStatus == 'SENDING'">
			  			<button type="button" class="btn btn-info btn-sm" data-toggle="modal" data-target=".chongxin" @click="gitid(moneyData.orderId,'addSystemRecord')"></button>
			  		</template>
        <template v-if="moneyData.workId != 0 && moneyData.orderStatus == 'SENDING'">
			  			<button type="button" class="btn btn-info btn-sm" data-toggle="modal" data-target=".chongxin" @click="gitid(moneyData.orderId,'againOrder')"></button>
			  		</template>
        <template v-if="moneyData.orderStatus == 'SERVER' || moneyData.orderStatus == 'WAIT_CHECK' || moneyData.orderStatus == 'WAIT_CONFIRM' || moneyData.orderStatus == 'REWORK'">
			  			<button type="button" class="btn btn-primary btn-sm" data-toggle="modal" data-target=".tuidan" @click="gitid(moneyData.orderId)"></button>
			  		</template>
        <template v-if="moneyData.orderStatus == 'CLOSE' || moneyData.orderStatus == 'FINISH'">
			  			<button type="button" class="btn btn-success btn-sm"  disabled="disabled"></button>
			  		</template>
    </td>
    </template>
    </tr>
    </table>
    <div v-if="show">
        
    </div>
    <page :turn-page="pageData"></page>
    <!--   star-->
    <div class="modal fade chedan" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
        <div class="modal-dialog modal-sm">
            <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
                    <h4 class="modal-title" id="myModalLabel"></h4>
                </div>
                <div class="modal-body">
                    <p><strong></strong> </p>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-default btn-sm" data-dismiss="modal"></button>
                    <button type="button" class="btn btn-primary btn-sm" data-dismiss="modal" @click="revokeOrder"></button>
                </div>
            </div>
        </div>
    </div>
    <!--   end-->
    <!--   star-->
    <div class="modal fade tuidan" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
        <div class="modal-dialog modal-sm">
            <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
                    <h4 class="modal-title" id="myModalLabel"></h4>
                </div>
                <div class="modal-body">
                    <p><strong></strong> </p>
                    <input type="number" style="width: 200px;" class="form-control" v-model="money" placeholder="" />
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-default btn-sm" data-dismiss="modal"></button>
                    <button type="button" class="btn btn-primary btn-sm" data-dismiss="modal" @click="cancelOrder"></button>
                </div>
            </div>
        </div>
    </div>
    <!--   end-->
    <!--   star-->
    <div class="modal fade chongxin" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
        <div class="modal-dialog modal-sm">
            <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
                    <h4 class="modal-title" id="myModalLabel"></h4>
                </div>
                <div class="modal-body" style="height: 220px;text-align: center;">
                    <div class="dropdown">
                        <p></p>
                        <input type="text" style="width: 200px;" class="form-control" v-model="workNickName" placeholder="" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" />
                        <ul class="dropdown-menu" role="menu" aria-labelledby="dLabel">
                            <template v-if="showFind">
				  		<div></div>
				  	</template>
                            <template v-else>
				  		<li v-for="workdata in workdata" style="padding:5px 10px;cursor: pointer;" @click="findoneWoek(workdata.workId)">{{workdata.workNickName}}</li>
				  	</template>
                        </ul>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-default btn-sm" data-dismiss="modal"></button>
                    <button type="button" class="btn btn-primary btn-sm" data-dismiss="modal" @click="addOrder(workOnedata.workId)"></button>
                </div>
            </div>
        </div>
    </div>
    <!--   end-->
    <!--   star-->
    <div class="modal fade yulan" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
        <div class="modal-dialog modal-lg">
            <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
                    <h4 class="modal-title" id="myModalLabel"></h4>
                </div>
                <div class="modal-body">
                    <img style="border-radius: 6px;border: 1px solid #ccc;" :src="domain + showPic +'?imageView2/2/w/200'" / v-for="showPic in oneData.showPic">
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-default btn-sm" data-dismiss="modal"></button>
                </div>
            </div>
        </div>
    </div>
    <!--   end-->
    </div>
    </template>
    <script type="text/javascript">
        const moment = require('moment')
        import Lib from '../assets/lib'
        import page from '../components/page/pagegai.vue'
        export default {
            props: {

            },
            data: () => ({
                domain: Lib.C.qiniu.jsUrl,
                host: Lib.C.hostBackstage,
                moneyData: {},
                photoName: '',
                pageNo: 1,
                show: false,
                showInupt: true,
                orderId: '',
                workNickName: '', //
                workId: '', //id
                workdata: {},
                money: '', //
                oneData: {},
                workOnedata: {},
                showFind: true,
                addOrdertype: '',
                orderStatus: '',
                pageData: {},
            }),
            created() {
                this.findMoney();
                this.findWork();
                this.getSocket();
            },
            ready() {

            },
            components: {
                page
            },
            events: {
                'child-turn-page' (obj) {
                    this.pageNo = obj.pageNo;
                    this.findMoney(this.orderStatus);
                },
            },
            watch: {
                'workNickName': function(val) {
                    this.findWork(val)
                },
                'money': function(val) {
                    if (val * 10 % 10 > 0) {
                        Lib.M.topRightInfoTips({
                            title: ''
                        });
                        this.money = 1;
                    }
                }
            },
            methods: {
                getSocket: function() {
                    var self = this;
                    var sockjsAddr = "118.178.236.192:8080" + "/scoket/join/" + "man0";
                    var sockjsClient;
                    var nickName = localStorage.getItem('position');
                    var onopen = function() {
                        console.log('open1')
                    };
                    var onmessage = function(e) {
                        console.log(e);
                        if (e) {
                            location.reload();
                        }
                    };
                    var onclose = function() {
                        console.log('close');
                        this.getSocketNum++;
                        if (this.getSocketNum < 10) {
                            this.getSocket();
                        }
                    };
                    // var sockjsClient = new WebSocket('ws://118.178.236.192:8080'+ "/scoket/join/"+"emp"+this.id;);
                    sockjsClient = new WebSocket('ws:' + sockjsAddr);
                    sockjsClient.onopen = onopen;
                    sockjsClient.onmessage = onmessage;
                    sockjsClient.onclose = onclose;
                },
                findMoney: function(orderStatus) {
                    this.orderStatus = orderStatus;
                    let url = this.host + 'onLinePs/orderInfo/findOrderInfo';
                    let self = this;
                    let params = {};
                    params = {
                        orderStatus: this.orderStatus,
                        condition: this.photoName,
                        pageNo: this.pageNo,
                        pageSize: 10,
                    };
                    Lib.M.load(url, params, function(data) {
                        if (data.hasOwnProperty('code')) {
                            self.show = true;
                        } else {
                            self.show = false;
                            self.$set('moneyData', data.data);
                            self.$set('pageData', data.data);
                            setInterval(function() {
                                self.daojishi();
                            }, 1000)
                        }
                    }, 'get');
                },
                daojishi: function() {
                    for (let i = 0; i < this.moneyData.list.length; i++) {
                        let dd, hh, mm, ss, over, countdown, overtime;
                        let one = this.moneyData.list[i];
                        let startime = one.startTime; //
                        let completetime = one.endTime; //
                        let addtime = one.urgentTime == 0 ? 72 : one.urgentTime;
                        if (addtime == 72) {
                            if (one.orderType == "ALL") {
                                addtime = 96
                            } else {
                                addtime = 72
                            }
                        }
                        let nowtime = moment()._d.getTime();
                        let endtime = moment(startime).add(addtime, 'h')._d.getTime(); //
                        if (one.orderStatus == 'FINISH') {
                            over = moment(completetime)._d.getTime() - moment(startime)._d.getTime();
                            dd = parseInt(over / 86400000);
                            hh = parseInt(over / 3600000);
                            mm = parseInt(over % 86400000 % 3600000 / 60000);
                            ss = parseInt(over % 86400000 % 3600000 % 60000 / 1000);
                            countdown = '<span style="color:#449d44">' + '' + hh + '' + mm + '' + ss + '</span>';
                            overtime = moment(completetime)._d.getTime() > endtime ? '<span style="color:red">()</span>' : ''; //
                            this.$set('moneyData.list[' + i + '].countdown', countdown + overtime);
                        } else {
                            if (nowtime > endtime) {
                                countdown = '<span style="color:red"></span>';
                                this.$set('moneyData.list[' + i + '].countdown', countdown);
                            } else {
                                over = endtime - nowtime;
                                dd = parseInt(over / 86400000);
                                hh = parseInt(over / 3600000);
                                mm = parseInt(over % 86400000 % 3600000 / 60000);
                                ss = parseInt(over % 86400000 % 3600000 % 60000 / 1000);
                                countdown = '' + hh + '' + mm + '' + ss + '';
                                this.$set('moneyData.list[' + i + '].countdown', countdown);
                            }
                        }
                    }
                },
                gitid: function(index, type) {
                    this.orderId = index;
                    this.money = '';
                    this.addOrdertype = type;
                },
                //
                cancelOrder: function() {
                    let url = this.host + 'onLinePs/orderInfo/cancelOrder';
                    let self = this;
                    let params = {};
                    params = {
                        money: this.money,
                        orderId: this.orderId,
                        systemId: localStorage.getItem('h_id')
                    };
                    Lib.M.load(url, JSON.stringify(params), function(data) {
                        if (data.code == -1) {
                            self.findMoney(self.findMoney);
                            Lib.M.topRightInfoTips({
                                title: data.message
                            });
                        } else {
                            Lib.M.topRightInfoTips({
                                title: data.message
                            });
                        }
                    }, 'put');
                },
                //
                revokeOrder: function() {
                    let url = this.host + 'onLinePs/orderInfo/revokeOrder';
                    let self = this;
                    let params = {};
                    params = {
                        orderId: this.orderId,
                        systemId: localStorage.getItem('h_id')
                    };
                    Lib.M.load(url, JSON.stringify(params), function(data) {
                        if (data.code == -1) {
                            self.findMoney(self.findMoney);
                            Lib.M.topRightInfoTips({
                                title: data.message
                            });
                        } else {
                            Lib.M.topRightInfoTips({
                                title: data.message
                            });
                        }
                    }, 'put');
                },
                //
                addOrder: function(workId) {
                    let url;
                    if (this.addOrdertype == 'againOrder') {
                        url = this.host + 'onLinePs/orderInfo/againOrder';
                    } else if (this.addOrdertype == 'addSystemRecord') {
                        url = this.host + 'onLinePs/orderInfo/addSystemRecord';
                    }
                    let self = this;
                    let params = {};
                    params = {
                        workId: workId,
                        orderId: this.orderId,
                        manage: localStorage.getItem('h_id') * 1
                    };
                    Lib.M.load(url, JSON.stringify(params), function(data) {
                        if (data.code == -1) {
                            self.findMoney(self.orderStatus);
                            Lib.M.topRightInfoTips({
                                title: data.message
                            });
                        } else {
                            Lib.M.topRightInfoTips({
                                title: data.message
                            });
                        }
                    }, self.addOrdertype == 'againOrder' ? 'put' : 'post');
                },
                //
                findWork: function(val) {
                    let url = this.host + 'onLinePs/workInfo/findWorkInfo';
                    let self = this;
                    let params = {};
                    params = {
                        pageNo: 1,
                        pageSize: 6,
                        status: 'YES',
                        workNickName: val,
                    };
                    Lib.M.load(url, params, function(data) {
                        if (data.hasOwnProperty('code')) {
                            self.showFind = true;
                        } else {
                            self.showFind = false;
                            self.$set('workdata', data.data.list)
                        }
                    }, 'get');
                },
                //
                findoneWoek: function(workid) {
                    let url = this.host + 'onLinePs/workInfo/findWorkInfo';
                    let self = this;
                    let params = {};
                    params = {
                        workId: workid,
                    };
                    Lib.M.load(url, params, function(data) {
                        self.$set('workOnedata', data.data.list[0])
                        self.$set('workNickName', data.data.list[0].workNickName)
                    }, 'get');
                },
                //
                findOneorder: function(orderId) {
                    let url = this.host + 'onLinePs/orderInfo/findOrderInfo';
                    let self = this;
                    let params = {};
                    params = {
                        orderId: orderId,
                    };
                    Lib.M.load(url, params, function(data) {
                        self.$set('oneData', data.data.list[0]);
                        if (self.oneData.showPic != '') {
                            self.oneData.showPic = JSON.parse(self.oneData.showPic)
                        } else {
                            self.oneData.showPic = []
                        }

                    }, 'get');
                },
            }
        }
    </script>
    <style type="text/css" scoped>
        .form-inline {
            margin-bottom: 10px;
        }
        
        tr th {
            text-align: center;
        }
        
        .time {
            width: 320px;
        }
        
        .moneyDetails a {
            text-decoration: none;
        }
        
        span {
            cursor: pointer;
        }
        
        .close {
            display: none;
        }
        
        .dropdown-menu li:hover {
            background: #7fc9e0;
        }
    </style>


