<template lang="html">
	<div class="moneyDetails">
		<h3 style="min-width:800px"></h3>
		<form class="form-inline" role="form" style="min-width:800px">
		  <div class="form-group">
		    <label class="sr-only"></label>
		    <input type="text" class="form-control time" placeholder=""  v-model="time">
		  </div>
		  <button type="button" class="btn btn-danger" @click="findMoney"></button>
		  <button type="button" class="btn btn-primary" @click="daochu"></button>
		</form>
		<table class="table table-bordered table-hover text-center table-striped">
		  <tr>
		  	<th style="min-width:200px"></th>
		  	<th style="min-width:200px"></th>
		  	<th style="min-width:200px"></th>
		  	<th style="min-width:200px"></th>
		  	<th style="min-width:200px"></th>
		  	<!--<th style="min-width:200px"></th>-->
		  </tr>
		  <tr v-for="moneyData in moneyData.list">
		  	<template v-if="!show">
		  		<td>{{$index+1}}</td>
			  	<td>{{moneyData.detail}}</td>
			  	<td>{{moneyData.createTime}}</td>
			  	<td>
			  		<template v-if="moneyData.operation == 'XT'">  </template>
			  		<template v-if="moneyData.operation == 'SJ'">  </template>
			  		<template v-if="moneyData.operation == 'CZ'">  </template>
			  		<template v-if="moneyData.operation == 'TX'">  </template>
			  		<template v-if="moneyData.operation == 'CF'">  </template>
			  		<template v-if="moneyData.operation == 'TD'">  </template>
			  		<template v-if="moneyData.operation == 'NONE'">  </template>
			  	</td>
			  	<td><template v-if="moneyData.type == 'REDUCE'"></template> <template v-if="moneyData.type == 'ADD'"></template>{{moneyData.money}}&nbsp;</td>
			  	<!--<td>{{moneyData.currentMoney.toFixed(2)}}</td>-->
		  	</template>

		  </tr>
		</table>
		<div v-if="show">
	  	
	  </div>
	  <page :turn-page="pageData"></page>
	</div>
</template>
<script type="text/javascript">
	import page from '../components/page/pagegai.vue'
	import Lib from '../assets/lib'
	require('daterangepicker')
	require('daterangepicker.css')
	const moment = require('moment')
export default {
	props: {

    },
	data: () => ({
		host: Lib.C.hostBackstage,
		addrUrl:Lib.M.getUrlParams(),
		moneyData:{},
		createTime:'',
		pageNo:1,
		time: moment().subtract(1,'month').format(Lib.C.dateLocale.format)+ '  ' +moment().format(Lib.C.dateLocale.format),
		show:false,
		pageData:{},
	}),
	created() {

	},
	ready (){
		this.timeWidgit()
	},
	components:{
		page
	},
	events: {
		'child-turn-page'(obj){
			this.pageNo = obj.pageNo;
			this.findMoney();
		},
    },
	watch: {

	},
	methods: {
		daochu (){
			if(this.addrUrl.photo){
				var photowork = 'photoId='+this.addrUrl.photo
			}else{
				var photowork = 'workId='+this.addrUrl.work
			}
			var a = document.createElement('a');
			var url = this.host + 'onLinePs/financeLog/exportToExcelDetail?fileName=&'+photowork+'&beginTime=' +this.time.split('  ')[0]+'&endTime='+this.time.split('  ')[1]
			a.href = url;
			a.target = '_blank';
			a.click();
			window.URL.revokeObjectURL(url); // url 
		},
		findMoney: function() {
            let url = this.host + 'onLinePs/financeLog/findFinanceLog';
            let self = this;
            let params = {};
            params = {
                beginTime: this.time.split('  ')[0],
                endTime: this.time.split('  ')[1],
                pageNo: this.pageNo ,
                pageSize: 15,
                photoId: this.addrUrl.hasOwnProperty('photo')?this.addrUrl.photo:'',
                workId: this.addrUrl.hasOwnProperty('work')?this.addrUrl.work:''
            };
            Lib.M.load(url, params, function(data) {
                if(data.hasOwnProperty('code')) {
                    self.show = true;
                } else {
                	self.show = false;
                    self.$set( 'moneyData',data.data )
                    self.$set( 'pageData',data.data )
                }
            },'get');
        },
		// 
		timeWidgit: function () {
			let locale = Lib.C.dateLocale;
			let tmp = locale.format;
			let startDate = moment().subtract(1,'month').format(tmp);
			let endDate = moment().format(tmp);
			this.$set('time', startDate + '  ' + endDate);
			$('.time').daterangepicker({
				timePicker: true,
				timePicker24Hour: true,
				locale: locale
			});
			this.findMoney();
		}
	}
}

</script>
<style type="text/css" scoped>
.form-inline{
	margin-bottom: 10px;
}
tr th{
	text-align: center;
}
.form-inline .time{
    width: 320px;
}
</style>


